body,
main {
  padding: 0;
  margin: 0 auto;
}
.visually-hidden {
  display: none;
}
@media (min-width: 1200px) {
  body {
    background-color: #f2f2f2;
  }
}
.page-header {
  background-color: #ffffff;
  position: relative;
}
.page-header__wrap {
  height: 56px;
  margin-top: 0;
  display: flex;
  justify-content: center;
}
.page-header__logo {
  z-index: 200;
}
.main-menu__toggle {
  position: absolute;
  display: block;
  width: 60px;
  height: 60px;
  background: url("../img/menu-cross.svg") no-repeat 50% 50%;
  background-size: 23px 22px;
  border: none;
  outline: none;
  cursor: pointer;
  right: 20px;
}
.main-menu__toggle--closed {
  background: url("../img/menu.svg") no-repeat 50% 50%;
  background-size: 23px 22px;
  border: none;
  outline: none;
  cursor: pointer;
  right: 20px;
  bottom: 0;
}
@media (min-width: 768px) {
  .page-header {
    width: 100%;
    padding: 0;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
  }
  .page-header__wrap {
    height: 0;
    order: 1;
  }
}
@media (min-width: 1200px) {
  .page-header {
    width: 1200px;
    padding: 0;
    margin: 0 auto;
  }
}
.main-menu {
  display: none;
}
@media (min-width: 768px) {
  .main-menu {
    display: block;
  }
}
.main-menu--opened {
  display: block;
}
.main-menu__list {
  padding: 0;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: flex-start;
  list-style-type: none;
}
.main-menu__item {
  background-color: #81b3d2;
  width: 100%;
  display: flex;
  justify-content: center;
  border-bottom: 1px solid #678fa8;
  border-top: 1px solid #9ac2db;
}
.main-menu__item:nth-last-child(1) {
  box-shadow: inset 0 -4px 4px 0 #6c96b0;
}
.main-menu__item a {
  color: #ffffff;
  font-size: 18px;
  line-height: 18px;
  font-weight: bold;
  font-family: PT Sans, "Arial", sans-serif;
  text-decoration: none;
  display: block;
  vertical-align: middle;
  padding-left: 0;
  padding-right: 0;
  padding-top: 18px;
  padding-bottom: 18px;
  text-transform: uppercase;
  flex-grow: 1;
  text-align: center;
}
.main-menu__item a:hover {
  background-color: #669ec0;
}
.main-menu__item a:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.302);
}
@media (min-width: 768px) {
  .main-menu {
    order: 2;
  }
  .main-menu__list {
    padding: 0;
    margin: 0 auto;
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    justify-content: flex-start;
    list-style-type: none;
  }
  .main-menu__item {
    background-color: #ffffff;
    width: 20%;
    display: block;
    border-bottom: none;
    border-top: none;
  }
  .main-menu__item:nth-last-child(1) {
    box-shadow: none;
  }
  .main-menu__item:nth-child(2) {
    margin-right: auto;
  }
  .main-menu__item a {
    color: #000000;
    font-size: 14px;
    line-height: 26px;
    font-weight: bold;
    font-family: "PT Sans", "Arial", sans-serif;
    vertical-align: middle;
    padding-left: 0;
    padding-right: 0;
    padding-top: 14px;
    padding-bottom: 16px;
    flex-grow: 0;
    text-align: center;
  }
  .main-menu__toggle {
    display: none;
  }
  .main-menu__item a:hover {
    color: #81b3d2;
    background-color: #ffffff;
  }
  .main-menu__item a:active {
    color: #b7b7b7;
    background-color: #ffffff;
  }
  .main-menu__item--active a {
    color: #ada19a;
  }
  .main-menu__item--active a:hover {
    color: #604e43;
  }
  .main-menu__item--active a:active {
    color: #d6d0cc;
  }
}
